import { useContext, useState} from "react";
import classes from "./Cart.module.css";
import Modal from "../UI/Modal";
import CartItem from "./CartItem";
import CartContext from "../../store/cart-context";
import Checkout from "./Checkout";

const Cart = (props) => {

  const cartContext = useContext(CartContext);

  const[checkoutInputValues, setCheckoutInputValues] = useState([]);

  const totalAmount = `${cartContext.totalAmount.toFixed(2)} руб.`;
  const hasItems = cartContext.items.length > 0;

  let formIsValid = false;

  const cartItemRemoveHandler = (id) => {
    cartContext.removeItem(id)
  };

  const cartItemAddHandler = (item) => {
    cartContext.addItem({...item, amount: 1})
  };

  const submitFormHandler = (event) => {
    event.preventDefault();

    console.log(checkoutInputValues);
  };

  const checkValidity = (status) => {
    formIsValid = status;
    
  }

  const getInputValues = (values) =>{
    const [nameInputValue, phoneInputIsValid, addressInputIsValid] = values;
    
    setCheckoutInputValues(values);
  }

  const cartItems = (
    <ul className={classes["cart-items"]}>
      {cartContext.items.map((item) => (
        <CartItem
          key={item.id}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
        />
      ))}
    </ul>
  );

  return (
    <Modal onHideCart={props.onHideCart}>
      {cartItems}
      <div className={classes.total}>
        <span>Всего:</span>
        <span>{totalAmount}</span>
      </div>
      {hasItems && <Checkout onCheckValidity={checkValidity} onInputsReady={getInputValues}/>}
      <div className={classes.actions}>
        <button onClick={props.onHideCart} className={classes["button--alt"]}>
          Закрыть
        </button>
        {hasItems && <button className={classes.button} form={props.form} onClick={submitFormHandler}>Подтвердить</button>}
      </div>
    </Modal>
  );
};

export default Cart;
